import {createApp} from 'vue';
import App from './App.vue';
import router from './router';
import ElementPlus from 'element-plus';
import 'element-plus/dist/index.css';
import * as ElIcons from '@element-plus/icons-vue';
import axios from 'axios'
import 'element-plus/theme-chalk/dark/css-vars.css'

// 设置基础URL
axios.defaults.baseURL = window.location.origin + '/api'
// axios.defaults.baseURL = 'http://127.0.0.1:8818/api'

// 配置请求拦截器
axios.interceptors.request.use(
    config => {
        // 获取 token（假设 token 存储在 localStorage 中）
        const token = sessionStorage.getItem('token');

        // 如果 token 存在，将它添加到请求头中
        if (token) {
            config.headers.Authorization = `Bearer ${token}`;
        }

        // 在发送请求前返回配置
        return config;
    },
    error => Promise.reject(error)
);

// 配置响应拦截器
axios.interceptors.response.use(
    response => response,
    error => {
        if (error.response && error.response.status === 401) {
            // 清除 token（可选）
            sessionStorage.removeItem('token');
            // 跳转到登录页
            window.location.href = '/login'; // 或者使用 Vue Router: this.$router.push('/login')
        }
        // 可以在这里进行错误处理
        return Promise.reject(error)
    }
)

const app = createApp(App);
app.use(router);
app.use(ElementPlus);
// 全局注册所有图标
for (const [key, component] of Object.entries(ElIcons)) {
    app.component(key, component)
}
app.config.globalProperties.$axios = axios // 将axios挂载到全局属性中

// 读取用户之前的设置（localStorage）
const theme = localStorage.getItem('theme') || 'light'
if (theme === 'dark') {
    document.documentElement.classList.add('dark')
}

// 提供一个方法切换主题
app.config.globalProperties.$toggleTheme = () => {
    const isDark = document.documentElement.classList.toggle('dark')
    localStorage.setItem('theme', isDark ? 'dark' : 'light')
}

app.mount('#app');
