import {createRouter, createWebHistory} from 'vue-router';

const routes = [
    {path: '/', redirect: '/login'},
    {path: '/login', component: () => import('@/components/login/UserLogin')},
    {
        path: '/dashboard',
        component: () => import('@/components/home/UserDashboard'),
        meta: {requiresAuth: true},
        children: [
            {path: '', component: () => import('@/components/home/OciHome')}, // 默认页面
            {path: 'home', component: () => import('@/components/home/OciHome')},
            {path: 'user', component: () => import('@/components/ociuser/OciUser')},
            {path: "createTask", component: () => import('@/components/task/OciCreateTask')},
            {path: "ociLog", component: () => import('@/components/log/OciLog')},
            {path: "sysCfg", component: () => import('@/components/syscfg/OciSysCfg')},
            {path: 'createInstance', component: () => import('@/components/instance/OciCreateInstance')},
            {path: 'OciCfgTab', component: () => import('@/components/ociuser/OciCfgTab')},
            {path: 'ociUpdateInstanceCfg', component: () => import('@/components/instance/OciUpdateInstanceCfg')},
            {path: "ociCreateInstanceBatch", component: () => import('@/components/instance/OciCreateInstanceBatch')},
        ]
    },
    {path: '/securityRuleList', component: () => import('@/components/vcn/SecurityRuleList')}
    ,
];

const router = createRouter({
    history: createWebHistory(),
    routes
});

// 路由守卫 - 全局拦截
router.beforeEach((to, from, next) => {
    const isAuthenticated = sessionStorage.getItem('token');

    if (to.matched.some(record => record.meta.requiresAuth) && !isAuthenticated) {
        // 如果页面需要登录且用户未登录，重定向到登录页
        next('/login');
    } else {
        if (window.ws) {
            console.log('Closing existing WebSocket connection before route change.');
            window.ws.close();
            window.ws = null;
        }
        next(); // 允许进入页面
    }
});

export default router;
